.header {
    background-color: var(--main-background);
    border-bottom: 2px solid rgba(96, 101, 99, 0.652);
}

.navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px linear;
}

.drawer {
    position: relative;
    overflow: hidden;
}

.logo-text {
    font-family: Lobster;
    font-size: 36px;
    color: #B38B59;
    // font-weight: 600;
}

.menu {
    font-weight: 600;
    font-size: 22px;
    line-height: 1.25;
    display: flex;
    gap: 30px;
    justify-content: center;
    align-items: center;

    @media screen and (max-width: 479px) {
        display: none;
    }
}

.menu-link {
    color: var(--light-text-color);
    transition: 0.3s;
}

.menu-link:hover,
.menu-link:focus {
    color: var(--accent-color);
    text-shadow: 1px 1px 2px rgba(120, 210, 158, 0.374);
    transform: translateY(-5px);
}

.burger-btn {
    background-color: var(--main-background);
    border-width: 0;
    margin-left: auto;
    position: relative;
    cursor: pointer;
    fill: #B38B59;
    
    @media screen and (min-width: 480px) {
        display: none;
    }

    &:hover {
        transform: scale(1.1);
    }
}



