  width: 100vw;
  margin: 0;
  font-family: Eb Garamond;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.75;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
    padding-left: 45px;
    padding-right: 45px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .container {
    max-width: 768px;
