    padding-right: 20px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.backdrop, .read-backdrop, .locations-backdrop, .franchise-backdrop {
  z-index: 10;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(86, 107, 98, .4);
  transition: opacity .4s, visibility .4s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-open {
  overflow: hidden;
}

.modal-open .backdrop, .modal-open .read-backdrop, .modal-open .locations-backdrop, .modal-open .franchise-backdrop {
  opacity: 0;
  visibility: hidden;
}

.modal-open .modal, .modal-open .read-modal, .modal-open .locations-modal, .modal-open .franchise-modal {
  opacity: 0;
  visibility: hidden;
  transform: translate(-50%, -50%)scale(0);
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop.is-visible, .read-backdrop.is-visible, .locations-backdrop.is-visible, .franchise-backdrop.is-visible {
  opacity: 1;
  visibility: visible;
}

.modal.is-visible, .read-modal.is-visible, .locations-modal.is-visible, .franchise-modal.is-visible {
  opacity: 1;
  visibility: visible;
  transform: translate(-50%, -50%)scale(1);
}

.modal, .read-modal, .locations-modal, .franchise-modal {
  background-color: var(--light-text-color);
  width: 568px;
  border: 2px solid var(--accent-color);
  opacity: 0;
  visibility: hidden;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  transition: transform .8s, opacity .8s, visibility .8s;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(0);
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .modal, .read-modal, .locations-modal, .franchise-modal {
    width: 280px;
    height: 100vh;
  }
